* {
  padding : 0;
  margin: 0;
  text-align: center;
}
ul{height: 100%;}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  height: 5vh;
  color: white;
}

.dateZone {
  height: 4vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(184, 182, 182);
}

.dateInfo {
  height: 2vh;
  width: 8vw;
  text-align: center;
  border-radius: 6px;
}

.listZone {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1vh;
  height: 90vh;
}

.mvlists {
  list-style: none;
  display: flex;
  flex-direction: column;
  height: 100%;
}


.mvlists > li {
  display: inline-block;
  margin: 15px;
  text-align: center;
  border : solid;
  border-width: 1px;
  width: 80vh;
  border-radius: 20px;
  padding: 1%;
}

.rank {
  float: left;
  margin-left: 10px;
  font-weight: bold;
}

.title {
  font-weight: bold;
  font-size: 15px;
  text-align: center;
}

.rankChange {
  float: right;
  font-size : 12px;
}

.infoLi {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 5vh;
  border-bottom: solid 1px;
}

.mkeys {
  font-weight: bold; 
}
.footer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn {
  margin: 30px;
  height: 3em;
  width: 8vw;
  background-color: white;
  border-color: black;
  border-radius: 5px;
}